@font-face {
  font-family: "Forum";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/Forum.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Rouble";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/rouble.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/GTWalsheimPro-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 500;
  src: url("fonts/GTWalsheimPro-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 700;
  src: url("fonts/GTWalsheimPro-Bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
  overflow: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.select {
  position: relative;
}
.select__result {
  border: 0 transparent;
  text-decoration: none;
  display: block;
  width: 100%;
  text-align: left;
  padding: 18px 26px 16px 20px;
  background-color: #ffffff;
  cursor: pointer;
  flex-shrink: 0;
  position: relative;
  z-index: 1;
  user-select: none;
}
.select__result::after {
  content: "";
  width: 14px;
  height: 9px;
  position: absolute;
  top: 49%;
  right: 2px;
  background: url("img/icons/select.svg") no-repeat center;
  background-size: 100%;
}
.select__result.active::after {
  transform: rotate(180deg);
}
.select__options {
  display: none;
  position: absolute;
  top: 50px;
  left: -10px;
  width: 110%;
  z-index: 0;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 0 4px 2px #342D2D29;
  text-align: left;
}
.select__options.active {
  display: flex;
}
.select__label {
  display: block;
  width: 100%;
  padding: 18px 26px 16px 12px;
  user-select: none;
}
.select__label:hover {
  cursor: pointer;
}
.select__input:not(:disabled):hover + label {
  background-color: #f5f4f2;
}
.select__input:not(:disabled):focus + label {
  outline: 1px solid #474442;
}
.select__input:disabled + label {
  opacity: 0.3;
  pointer-events: none;
}
.subscribe {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #5f5f5f;
  font-weight: 400;
  text-align: center;
  margin-top: 24px;
  padding: 0 21px;
  background: #ffffff linear-gradient(180deg, #ffffff 53%, #f5f4f2 53% 100%) no-repeat top;
  background-size: cover;
}
@media (min-width: 1280px) {
  .subscribe {
    font-size: 16px;
    line-height: 21px;
    color: #5f5f5f;
    font-weight: 400;
  }
}
.subscribe__container {
  background-color: #ffffff;
  border: 1px solid #E0DEDA;
  width: 100%;
  max-width: 982px;
  margin: 0 auto;
  padding: 24px 32px 23px;
}
@media (min-width: 1280px) {
  .subscribe__container {
    padding: 46px 99px 46px;
  }
}
.subscribe__title {
  font-family: "Forum", "Georgia", serif;
  font-size: 32px;
  line-height: 35px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  word-break: break-word;
  max-width: 90%;
  margin: 0 auto 7px;
  padding: 0;
}
@media (min-width: 1280px) {
  .subscribe__title {
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 9px;
  }
}
.subscribe__form {
  margin-bottom: 24px;
}
@media (min-width: 1280px) {
  .subscribe__form {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 33px;
  }
}
.subscribe__input,
.subscribe__select {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #5f5f5f;
  font-weight: 400;
  letter-spacing: -0.02em;
  border: none;
  border-bottom: 1px solid #5f5f5f;
  transition: border-bottom 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__description {
  margin: 0 auto 15px;
  width: 100%;
  max-width: 560px;
  letter-spacing: 0.06px;
}
@media (min-width: 1280px) {
  .subscribe__description {
    margin: 0 auto 32px;
  }
}
.subscribe__input {
  width: 100%;
  padding: 18px 26px 16px 0;
}
.subscribe__input:focus {
  color: #474442;
  border-color: #474442;
}
@media (min-width: 1280px) {
  .subscribe__input {
    padding-right: 40px;
    padding-bottom: 14px;
  }
}
.subscribe__label {
  position: relative;
  display: block;
  margin-bottom: 8px;
}
@media (min-width: 1280px) {
  .subscribe__label {
    margin-bottom: 0;
    margin-right: 22px;
    flex-basis: 33%;
  }
}
.subscribe__select {
  width: 100%;
  margin-bottom: 32px;
}
@media (min-width: 1280px) {
  .subscribe__select {
    margin-bottom: 0;
    margin-right: 22px;
    flex-basis: 33%;
  }
}
.subscribe__select button {
  border: 0 transparent;
  text-decoration: none;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #5f5f5f;
  font-weight: 400;
  letter-spacing: -0.02em;
  padding: 18px 26px 16px 0;
  height: 100%;
}
.subscribe__select button:active {
  color: #474442;
  border-color: #474442;
}
@media (min-width: 1280px) {
  .subscribe__select button {
    padding-bottom: 14px;
  }
}
.subscribe__error {
  position: absolute;
  left: 0;
  top: calc(100% + 12px);
  text-align: left;
  color: #e07c66;
  display: none;
}
.subscribe__label.error .subscribe__input {
  color: #474442;
  border-bottom: 1px solid #e07c66;
}
.subscribe__label.error .subscribe__error {
  display: block;
}
.subscribe__input {
  line-height: 22px;
}
.subscribe__submit {
  background-color: #474442;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  font-weight: 500;
  padding: 17px 22px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__submit:hover {
  cursor: pointer;
}
@media (min-width: 1280px) {
  .subscribe__submit {
    padding: 17px 98px;
    margin-left: auto;
    max-width: 294px;
  }
}
.subscribe__submit:hover {
  background-color: #1A1716;
  color: #ffffff;
}
.subscribe__submit:active {
  background-color: #5f5f5f;
  color: #acacac;
}
.subscribe__agreement {
  margin: 0;
  padding: 0;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #5f5f5f;
  font-weight: 400;
}
.subscribe__agreement a {
  color: #5f5f5f;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.subscribe__agreement a:hover {
  opacity: 0.5;
}
.instagram {
  text-align: center;
  background-color: #f5f4f2;
  font-family: "Forum", "Georgia", serif;
  padding: 49px 0;
}
@media (min-width: 1280px) {
  .instagram {
    padding-top: 84px;
  }
}
.instagram__container {
  overflow: hidden;
}
.instagram__title {
  font-size: 32px;
  line-height: 35px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  margin: 0 0 4px -4px;
}
@media (min-width: 768px) {
  .instagram__title {
    font-size: 48px;
    line-height: 48px;
    margin: 0 auto 4px auto;
  }
}
.instagram__link {
  display: inline-block;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: #5F5F5F;
  font-weight: 400;
  letter-spacing: 0.02em;
  margin: 0 0 30px -2px;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.instagram__link:hover {
  opacity: 0.5;
}
@media (min-width: 1280px) {
  .instagram__link {
    font-size: 20px;
    line-height: 24px;
    margin: 0 -8px 57px 0;
  }
}
.instagram__slider {
  height: 230px;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1280px) {
  .instagram__slider {
    height: 278px;
    margin-bottom: 48px;
  }
}
.instagram__button-wrapper {
  position: relative;
  min-height: 160px;
}
@media (min-width: 768px) {
  .instagram__button-wrapper {
    min-height: 178px;
  }
}
.instagram__button-wrapper::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  height: 1px;
  background-color: #e0deda;
  width: 100%;
  z-index: 1;
}
.instagram__button {
  border: 0 transparent;
  text-decoration: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 160px;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border-radius: 50%;
  font-family: "Forum", "Georgia", serif;
  font-size: 40px;
  line-height: 36px;
  color: #474442;
  font-weight: 400;
  letter-spacing: -0.02em;
  z-index: 2;
  transition: background-color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95), color 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.instagram__button:hover {
  background-color: #474442;
  color: #ffffff;
  cursor: pointer;
}
.instagram__button:active {
  background-color: #5f5f5f;
  color: #acacac;
}
@media (min-width: 1280px) {
  .instagram__button {
    width: 178px;
    height: 178px;
  }
}
.instagram__button-text {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: 500;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  margin-top: 10px;
}
.swiper-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.swiper-slide {
  flex-shrink: 0;
  margin-right: 24px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  left: auto;
  display: none;
}
.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  right: auto;
  display: none;
}
.swiper-pagination {
  display: none;
}
.footer {
