.footer__nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.footer__nav p {
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #474442;
  font-weight: 700;
  margin: 0 0 9px;
}
@media (min-width: 1280px) {
  .footer__nav p {
    margin-bottom: 15px;
  }
}
.footer__nav a {
  display: inline-block;
  text-decoration: none;
  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #474442;
  font-weight: 400;
  transition: opacity 0.3s cubic-bezier(0.45, 0.05, 0.55 0.95);
}
.footer__nav a:hover {
  opacity: 0.5;
}
.footer__nav a:not(:last-child) {
  margin-bottom: 10px;
}
@media (min-width: 1280px) {
  .footer__nav a:not(:last-child) {
    margin-bottom: 15px;
  }
}
@media (min-width: 1280px) {
  .footer__nav {
    display: grid;
    grid-template-columns: repeat(auto-fill, 31%);
    column-gap: 8px;
    row-gap: 10px;
    width: 68%;
  }
}
.footer__nav-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 1280px) {
  .footer__nav-item {
    align-items: flex-start;
  }
}
.footer__nav-item:not(:last-child) {
  margin-bottom: 25px;
}
@media (min-width: 1280px) {
  .footer__nav-item:not(:last-child) {
    margin-bottom: 0;
  }
}
.footer__socials {
