  font-family: "GT Walsheim Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 15px;
  color: #474442;
  font-weight: 400;
  text-decoration: none;
  margin-bottom: 24px;
  position: relative;
}
@media (min-width: 1280px) {
  .footer__politics {
    margin-bottom: 0;
    transform: translateX(-16%);
  }
}
.footer__politics::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: #474442;
  display: none;
}
@media (min-width: 1280px) {
  .footer__politics::after {
    display: block;
  }
}
